.sendForm {
    display: flex;
}

.textarea {
    width: 976px;
    min-height: 40px;
    border-radius: 20px;
    resize: none;
    background-color: #F5F7FB;
}

.textarea::placeholder {
    font-size: 14px;
    font-weight: 400;
}

.button {
    width: 120px;
    height: 40px;
    border-radius: 20px;
    background-color: #0066CC;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    color: #FFF;
}

.button:hover {
    cursor: pointer;
}

.button:active {}